        </v-layout>
      </v-card-title>
      <!-- 
  headers : Ë°®Â§¥
  itemsÔºö Ë°®Ê†ºÂÜÖÂÆπ
  total-items Ôºö ÊÄªÂÖ±ÁöÑÊï∞ÊçÆÊï∞Èáè ÔºàÊâãÂä®ËÆæÁΩÆÈ°πÁõÆÁöÑÊÄªÊï∞ÔºåÂ∞±ÂèØ‰ª•Á¶ÅÁî®ÂÜÖÁΩÆÊéíÂ∫èÂíåÂàÜÈ°µÔºâ
  disable-initial-sort Âú®ÂàùÂßãÊ∏≤ÊüìÊó∂Á¶ÅÁî®ÈªòËÆ§ÊéíÂ∫è
  :hide-actions="true" ÈöêËóèË°®Ê†ºÊìç‰Ωú
  :dark="true" Êöó‰∏ªÈ¢ò
