        <!-- ÂΩìÊ≤°ÊúâÊï∞ÊçÆÁöÑÊó∂ÂÄôÔºåÂèØ‰ª•‰ΩøÁî® no-data ÊèíÊßΩÊòæÁ§∫‰∏ÄÊÆµËá™ÂÆö‰πâHTML„ÄÇ -->
        <template v-slot:no-data>
          <!-- <v-alert :value="true" color="warning" icon="priority_high" outline>Ê≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ‰∫Ü :(</v-alert> -->
          <p class="p_text" > Ê≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ‰∫Ü :( </p>
        </template>
        <!-- Â¶ÇÊûú‰Ω†ËøòÊÉ≥Ê∑ªÂä†‰∏Ä‰∫õÈ¢ùÂ§ñÁöÑÂäüËÉΩÔºåÊØîÂ¶ÇÊØèÂàóËøáÊª§ÊàñËÄÖÊêúÁ¥¢Ôºå‰Ω†ÂèØ‰ª•‰ΩøÁî® footer ÊèíÊßΩ„ÄÇ -->
        <!-- <template v-slot:footer>
          <td :colspan="headers.length">
            <strong>ÊÄªËÆ°Ôºö</strong>
          </td>
        </template> -->
        <!-- ‰Ω†ÂèØ‰ª•‰ΩøÁî®page-text ÊèíÊßΩÊù•Ëá™ÂÆö‰πâÊòæÁ§∫ËåÉÂõ¥‰ª•ÂèäÊÄªÊù°ÁõÆ„ÄÇ -->
        <template v-slot:pageText="props">ÂΩìÂâç {{ props.pageStart }} - {{ props.pageStop }} ÊÄªÂÖ± {{ props.itemsLength }}</template>
      </v-data-table>
