    <!-- Á°ÆËÆ§Ê°Ü -->
      <v-dialog v-model="dialog_logout" max-width="290">
        <v-card>
          <v-card-title class="headline">Âà†Èô§Êìç‰Ωú</v-card-title>
          <v-card-text> Á°ÆËÆ§Âà†Èô§ËØ•Ë¥¶Âè∑ÂêóÔºüÁ°ÆËÆ§Âà†Èô§Â∞Ü‰∏çÂèØÊÅ¢Â§ç </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" flat="flat" @click="dialog_logout = false">ÂèñÊ∂à</v-btn>
            <v-btn color="green darken-1" flat="flat" @click="delete_manage">Á°ÆËÆ§Âà†Èô§</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
