</template>
<script>
import Breadcrumbs from "../../components/breadcrumbs/breadcrumbs";
import Module from "./components/module.vue";
import Module1 from "./components/module1.vue";
import Module2 from "./components/module2.vue";
import Api from "../../server/api.js";
export default {
  components: {
    Breadcrumbs,
    Module,
    Module1,
    Module2
  },
  data() {
    return {
      NAME:"",

      // Á°ÆËÆ§Ê°Ü
      dialog_logout: false,
      id: null,

      pagingList: [100,{ text: "100", value: 100 }],
      loader: false,
      page: 0,
      items: [
        {
          text: "È¶ñÈ°µ",
          disabled: false,
          to: "/home"
        },
        {
          text: "Â≠êË¥¶Âè∑‰∫∫ÂëòÂàóË°®",
          disabled: false,
          to: "/account"
        }
      ],
      headers: [
        {
          text: "#",
          align: "left",
          sortable: false, // ÊòØÂê¶ÂèØÊéíÂ∫è
          value: "name"
        },
        { text: "Ë¥¶Âè∑", value: "fat", sortable: false },
        { text: "ÁúüÂÆûÂßìÂêç", value: "fat", sortable: false },
        { text: "ËßíËâ≤", value: "calories", sortable: false },
        { text: "ÊâãÊú∫", value: "carbs", sortable: false },
        { text: "ÈÇÆÁÆ±", value: "protein", sortable: false },
        { text: "ÂàõÂª∫Êó∂Èó¥", value: "iron", sortable: false },
        { text: "Êìç‰Ωú", value: "iron", sortable: false }
      ],
      desserts: [],
      root_path: this.$cookie.getLocalStorage("root_path").root_path
    };
  },
  computed:{
    // ÂâçÁ´ØÁ≠õÈÄâ
    desserts_: function(){
      return this.desserts.filter(( blog )=>{
        return blog.true_name.match(this.NAME);
      })
    }

  },
  methods: {
    loader_() {
      this.loader = true;
      setTimeout(() => (this.loader = false), 500);
    },
    info(event) {
      this.loader = true;
      this.$axios({
        method: "post",
        url: Api.URL.manages_query_manage,
        data: {
          id: -1,
          level: 1
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.desserts = response.data.datas;
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
          this.loader = false;
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: "üò°" + error,timeout: 3000, snackbar: true });
        });
    },
    // Â≠êÁªÑ‰ª∂‰∏≠Ëß¶Âèë
    adminAdd() {
      this.info();
    },
    // ‰øÆÊîπË¥¶Âè∑‰ø°ÊÅØ
    modifyMessage(data){
      let datas = {
        state:true,
        list: data
      }
      this.$refs.module2.dialogShow(datas);
    },
    // Êü•ÁúãË¥¶Âè∑ËØ¶ÊÉÖ
    infoMessage( data ){
      let datas = {
        state:true,
        list: data
      }
      this.$refs.module1.dialogShow(datas);
    },
    // Âà†Èô§Á°ÆËÆ§Ê°Ü
    delMessage( id ){
      this.id = id;
      this.dialog_logout = true;
    },
    // Âà†Èô§ÂëòÂ∑•Êàñ‰ª£ÁêÜË¥¶Âè∑ manages/delete_manage
    delete_manage(  ){
this.$axios({
        method: "post",
        url: Api.URL.manages_delete_manage,
        data: {
          id: this.id
        }
      })
        .then(response => {
          //ËØ∑Ê±ÇÊàêÂäüËøîÂõûÁöÑÊï∞ÊçÆ
          if (response.data.result === 0) {
            this.$Dialog.getProductCheckResult_({text: "Ë¥¶Âè∑Â∑≤ÊàêÂäüÂà†Èô§",timeout: 3000, snackbar: true });
            this.info();
            this.dialog_logout = false;
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: "üò°" + error,timeout: 3000, snackbar: true });
        });
    }
  },
  watch: {}
};
</script>

