  },
  methods: {
    roleList() {
      this.$axios({
        method: "post",
        url: this.$apis.URL.manages_query_role_list,
        data: {}
      })
        .then(response => {
          //ËØ∑Ê±ÇÊàêÂäüËøîÂõûÁöÑÊï∞ÊçÆ

          if (response.data.result === 0) {
            
            response.data.datas.forEach(item => {
              this.rolesList.push(item.role_name);
            });
            this.rolesData = response.data.datas;
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: "üò°" + error,timeout: 3000, snackbar: true });
        });
    },
      // Ê∑ªÂä†Ë¥¶Âè∑Êìç‰Ωú
      addManages(){
        if( this.userData.user_name.length < 5 ){ this.$Dialog.getProductCheckResult_({text: "üò°" + "Ë¥¶Âè∑‰∏∫Â≠óÊØç/Êï∞Â≠óÁªÑÂêàÔºà5-16Ôºâ‰Ωç",timeout: 3000, snackbar: true }); return false; }
        if( this.userData.password.length < 5 ){ this.$Dialog.getProductCheckResult_({text: "üò°" + "ÂØÜÁ†Å‰∏∫Â≠óÊØç/Êï∞Â≠óÁªÑÂêàÔºà6-26Ôºâ‰Ωç",timeout: 3000, snackbar: true }); return false; }
         if( this.userData.true_name.length < 2 ){ this.$Dialog.getProductCheckResult_({text: "üò°" + "ËØ∑ËæìÂÖ•ÁúüÂÆûÂßìÂêçÔºà2-16Ôºâ‰∏™Â≠óÁ¨¶",timeout: 3000, snackbar: true }); return false; }
        if( this.userData.email.length < 5 ){ this.$Dialog.getProductCheckResult_({text: "üò°" + "ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÈÇÆÁÆ±Âú∞ÂùÄ",timeout: 3000, snackbar: true }); return false; }
        if( this.userData.phone.length !== 11 ){ this.$Dialog.getProductCheckResult_({text: "üò°" + "ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÊâãÊú∫Âè∑Á†Å",timeout: 3000, snackbar: true }); return false; }
        if( this.userData.gender.length == "" ){ this.$Dialog.getProductCheckResult_({text: "üò°" + "ËØ∑ÈÄâÊã©ÁÆ°ÁêÜ/‰ª£ÁêÜ‰∫∫ÂëòÊÄßÂà´",timeout: 3000, snackbar: true }); return false; }
        if( this.userData.role_id.length == "" ){ this.$Dialog.getProductCheckResult_({text: "üò°" + "ËØ∑ÈÄâÊã©‰∏Ä‰∏™ÊùÉÈôêËßíËâ≤",timeout: 3000, snackbar: true }); return false; }
        
        // Ëß£ÊûêËßíËâ≤ID
        this.rolesData.forEach(item => {
          if( this.userData.role_id == item.role_name ){
            this.userData.role_id = item.role_id;
            return false;
          }
        });
        this.userData.gender=="Áî∑"?this.userData.gender = 1:this.userData.gender = 1;
        this.$axios({
        method: "post",
        url: this.$apis.URL.manages_add_manage,
        data: this.userData
      })
        .then(response => {
          //ËØ∑Ê±ÇÊàêÂäüËøîÂõûÁöÑÊï∞ÊçÆ
          if (response.data.result === 0) {
            this.$Dialog.getProductCheckResult_({text: `Â∑≤Ê∑ªÂä†‰∫ÜÊñ∞ÁöÑË¥¶Âè∑${ this.userData.user_name }`,timeout: 3000, snackbar: true });
            this.$parent.$parent.info();
            this.dialog = false;
          } else {
            this.$Dialog.getProductCheckResult_({text: "üò°" + response.data.message,timeout: 3000, snackbar: true });
          }
          this.userData.gender = "";
          this.userData.role_id = "";
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: "üò°" + error,timeout: 3000, snackbar: true });
        });
      } 
  },
};
</script>

